*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@font-face {
  font-family: 'Open Sans';
  src: url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');
}

body {
  font-family: 'Open Sans', sans-serif;
}
.mainContainer{
  display: flex;
  justify-content: space-between;
    max-width: 1250px;
  margin: 0 auto;
  height: 100vh;
  flex-direction: column;
  align-items: center;
}
span{
  font-family: 'Kumbh Sans';
font-style: normal;
font-weight: 400;
font-size: 15px;
line-height: 26px;
color: #69707D;
opacity: 1;
}
span:hover{
  cursor: pointer;
  color: #1D2026
}
.header{
  width: 100%;
  height: 120px;
  display: flex;
  border-bottom: 2px solid #E4E9F2;
}
.left-side{
  display: flex;
  width: 70%;
  align-items: center;
  justify-content: space-evenly;
}
.right-side{
  width: 30%;
  display: flex;
  gap: 46px;
  justify-content: flex-end;
  align-items: center;
}
.pfp{
  width: 50px;
  height: 50px;
margin-right: 70px;
}
.Collections{
  width: 90px;
  height: 117px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: solid 5px rgb(255, 96, 4,0);
    
}
.Collections:hover{
  border-bottom: solid 4px rgb(255, 96, 4,1);
}
.section{
  width: 1015px;
  height: 565px;
  display: flex;
}
.pfpDiv{
  width: 55px;
  height: 55px;
  border-radius: 50%;
}
.pfpDiv:hover{
  cursor: pointer;
  border: orange solid 3px;
}
.cart:hover{
  cursor: pointer;
}
.small-image {
  width: 88px;
  height: 88px;
  border-radius: 10px;
  cursor: pointer;
}
.small-image:hover{
  opacity: 0.3;
}
.big-image{
  width: 445px;
height: 445px;
border-radius: 10px;
}
.small-images {
  display: flex;
  gap: 31px;
}
.left-section{
  width: 50%;
}
.right-section{
  width: 50%;
  display: flex;
  flex-direction: column;
    justify-content: space-evenly;
}
.gallery{
  width: 100%;
}
.p1{
  font-family: 'Kumbh Sans';
font-style: normal;
font-weight: 700;
font-size: 13px;
line-height: 16px;
letter-spacing: 2px;
text-transform: uppercase;

color: #FF7E1B;

}
.p2{
  font-family: 'Kumbh Sans';
font-style: normal;
font-weight: 700;
font-size: 44px;
line-height: 48px;
/* or 109% */


color: #1D2026;
}
.p3{
  font-family: 'Kumbh Sans';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 26px;
/* or 162% */


color: #69707D;
}
.p4{
  font-family: 'Kumbh Sans';
font-style: normal;
font-weight: 700;
font-size: 28px;
line-height: 35px;
/* identical to box height */


color: #1D2026;
}
.percent{
  font-family: 'Kumbh Sans';
font-style: normal;
font-weight: 700;
font-size: 16px;
line-height: 20px;

color: #FF7E1B;
width: 51px;
height: 27px;
background-color: #FFEEE2;
border-radius: 6px;
display: flex;
justify-content: center;
align-items: center;
}
.minus{
  width: 12px;
  height: 3.3px;
  background-color: #FF7E1B;
}
.plus{
  width: 13px;
  height: 13px;
  color: #FF7E1B;
}
.sixthText{
display: flex;
justify-content: space-around;
}
.plusMinus{
  display: flex;
  background: #F6F8FD;
  border-radius: 10px;
  width: 157px;
  height: 56px;
  align-items: center;
  justify-content: space-around;
}
.addToCart{
  background: #FF7E1B;
box-shadow: 0px 20px 50px -20px #FF7E1B;
border-radius: 10px;
width: 272px;
height: 56px;
display: flex;
align-items: center;
justify-content: center;
}
.p5{
  font-family: 'Kumbh Sans';
font-style: normal;
font-weight: 700;
font-size: 16px;
line-height: 20px;
color: #FFFFFF;
}